:host {
  display: block;

  .gcds-fieldset {
    border: none;
    padding: 0;
    color: var(--gcds-fieldset-default-text);

    & > legend {
      font: var(--gcds-fieldset-font);
      color: inherit;
      margin: var(--gcds-fieldset-legend-margin);
    }

    &:focus-within {
      color: var(--gcds-fieldset-focus-text);
    }

    &:disabled {
      color: var(--gcds-fieldset-disabled-text);
    }
  }

  .legend__required {
    margin: var(--gcds-fieldset-legend-required-margin);
  }

  slot {
    display: block;
    margin: 0;
  }
}
